/* Clock.css */
body, html {
    height: 100%;
    font-family: 'Courier New', monospace;
  }
  
  .flex {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .min-h-screen {
    min-height: 100vh;
  }
 
  
  .text-indigo {
    color: #312e81;
  }
  
  .clock-container {
    position: relative;
    width: 18rem;
    height: 15rem;
    background-color: #7c99d3;
    padding: 1.5rem;
    box-shadow: inset 4px 4px 8px rgba(0, 0, 0, 0.5), 
                inset -4px -4px 8px rgba(0, 0, 0, 0.5);
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    align-items: center;
    text-align: center;
  }
  
  .day,
  .full-date {
    text-transform: uppercase;
    font-weight: 700;
    color: white;
  }
  
  .time-box {
    color: white;
    padding: 0.75rem;
    border-radius: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.25rem;
  }
  
  .time-box span {
    font-size: 1.75rem;
    font-weight: 800;
  }
  
  .meridiem {
    font-size: 0.875rem;
    background-color: rgba(255, 255, 255, 0.2);
    padding: 0.25rem;
    border-radius: 0.5rem;
  }
  
  .seconds-box {
    font-size: 1rem;
    color: white;
  }
  